.contaiabaou{
    width: 100vw;
    height: auto;
    background-image: url('../../public/bg.png');
    background-position: center;
    background-size: cover;
   
}
.containerAbout{
   
    margin: auto;
    width: 90vw;
    height: 100vh;
    overflow-y: hidden;
    background-image: url(../../public/G.png);
    background-size: 400px;
    background-repeat: no-repeat;
    background-position: center;
    display: grid;
    grid-template-columns: auto 15% auto;
    align-items: center;
    
}
.box{
    display: grid;
    justify-content: center;
    
}
.boxAbout{
    text-align: end; 
}
.box h1{
    font-family: 'Cinzel', serif;
    font-size: 4em;
    padding-bottom: 4vh;
    color: rgb(196, 172, 131);
}
.box p{
    font-size: 0.8em;
    max-width: 700px;
}
.box button{
    padding: 10px;
    width: 10vw;
    margin-top: 2vh;
}
.btnleft{
    margin-left: 73%;
}
.box .img{
    margin: auto;
    height: 85vh;
}
