.ConatMenu{
    width: 100vw;
    height: auto;
    padding-top: 4vh;
    background-color: rgb(15, 13, 13);
   
}
.Conatiner{
    width: 95vw;
    margin: auto;
    
}
.Conatinerh2{
    font-family: 'Cinzel', serif;
    text-align: center;
    font-size: 1.2em;
}
.Conatinerimg{
    margin-left: 48%;
    width: 50px;
}
.Conatinerh1{
    text-align: center;
    font-size: 4em;
    font-family: 'Cinzel', serif;
    font-weight: 500;
    padding-top: 2vh;
    padding-bottom: 4vh;
    color: rgb(196, 172, 131);
}
.content{
    display: grid;
    grid-template-columns: auto 40% auto;
    justify-content: space-around;
    align-items: start;
}
.content img{
    margin: auto;
    height: 80vh;
}
.boxsimg{
    display: grid;
    justify-content: center;
}
.boxs{
    width: 28vw;
}
.boxs h2{
    text-align: center;
    font-family: 'Cinzel', serif;
    font-size: 3em;
    margin-bottom: 3vh;
}
.itens{
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    margin-bottom: 2vh;
}
.itens h4{
    color: rgb(196, 172, 131);
    font-family: 'Cinzel', serif;
    font-size: 1.2em;
}
.itens p{
    color: rgb(109, 108, 108);
    font-size: 0.8em;
}