#navbar {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: rgb(175, 168, 168);
    position: fixed;
}
.ativaCor{
    background-color: black;
    
}

.link{
    padding: 4vh;
    text-decoration: none;
    color: white;
    margin-right: 0px;
   
}
.link:hover{
    color: rgb(107, 105, 102);
}
.links img{
    width: 150px;
    margin-left: 4vw;
}
.log {
    display: none;
}
@media (max-width: 1200px) {
    
}

@media (max-width: 870px) {
    
    .logo{
        font-size: 1.5em;
        margin-left:2vw;
    }
    .link{
        padding: 2vh;
        text-decoration: none;
        color: white;
        margin-right: 0px;
       
    }

}
@media (max-width: 670px) {
    
    .link{
        padding: 1vh;
        text-decoration: none;
        color: white;
        margin-right: 0px;
       
    }

}
@media (max-width: 570px) {
    
    .link{
        padding: 1vh;
        text-decoration: none;
        color: white;
        margin-right: 0px;
       
    }
    .dds{
        display: none;
    }

}

@media (max-width: 480px) {
    
    .logo{
        font-size: 1.5em;
        margin-left:2vw;
    }
    .link{
        padding: 0vh;
        margin-right: 2vw;
    }

}