.contaiPhoto{
    width: 100vw;
    height: auto;
    background-image: url("../../public/bg.png");
}

.containerPhoto{
    margin-top: 0vh;

    width: 98vw;
    margin: auto;
    height: 80vh;
    display: grid;
    grid-template-columns: 30% 55%;
    justify-content: space-around;
    align-items: center;
}
.itensText h3{
    color: rgb(196, 172, 131);
    font-size: 1.3em;
}
.photoboxImg{
    overflow: hidden;
    height: 45vh;
    width: 55vw;
    display: flex;
    
}
.photoboxImgImg{
    width: 16vw;
    margin-right: 1vw;
    height: 100%;
    border: 1px solid black;
    background-image: url("../../public/gallery01.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
