/* find us */

.containerFinds{
    
    width: 90vw;
    margin: auto;
    height: 100vh;
    display: grid;
    grid-template-columns: auto  auto;
    justify-content: space-around;
    align-items: center;
}
.boxText h4 {
    font-size: 1em;
    color: rgb(196, 172, 131);
    margin-bottom: 25px;
}
.boxFindImg img{
    width: 600px;
}