.contaiMain{
    height: auto;
    width: 100vw;
    /* background-image: url("../../public/bg.png"); */
    background-color: black;
    color: white;
    
}
.conatinermain{
    height: 100vh;
    display: grid;
    padding-top: 10vh;
    grid-template-columns: auto auto;
    justify-content: space-around;
    align-items: center;
}
.boxText{
    width: 40vw;
    height: auto;
    padding-bottom: 40px;
}

.boxText h3{
    font-family: 'Cinzel', serif;
    font-size: 1.2em;
}
.imgHeander{
    width: 600px;
}
.boxText h1{
    font-family: 'Cinzel', serif;
    font-size: 4.8em;
    font-weight: 600;
    padding-top: 2vh;
    padding-bottom: 2vh;
    color: rgb(196, 172, 131);

}
.boxText p {
    width: 35vw;
    font-size: 0.8em;
    margin-bottom: 4vh;
}
.boxText button{
    padding: 10px;
    width: 20vw;
    border-radius: 3px;
    color: rgb(109, 113, 117);
}

@media (max-width: 1020px) {
    .conatinermain{
        padding-top: 15vh;
        padding-bottom: 10vh;
        height: auto;
        display: grid;
        grid-template-columns: auto ;
        justify-content: space-around;
        align-items: center;
    }
    .headerImg{
        margin: auto;
        width: 400px;
    }
    .boxText {
        width: 90vw;
        
        padding-bottom: 80px;
    }
    .boxText p {
        width: 65vw;
        
    }
    
}
@media (max-width: 660px) {
    
    .boxText button {
        width: 50vw;
        
    }
    .imgHeander{
        width: 400px;
    }
    
}
@media (max-width: 470px) {
    
    .boxText button {
        width: 45vw;
        
    }
    .imgHeander{
        width: 250px;
    }
    .boxText h1 {
        font-family: 'Cinzel', serif;
        font-size: 3em;
        
    }
    .boxText p {
        width: 85vw;
    }
    
}