.ConatAwards{
    width: 100vw;
    height: auto;
}
.Container{
    width: 80vw;
    margin: auto;
    height: 100vh;
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    align-items: center;
}
.boxImg img{
    width: 550px;
}
.boxText h1{
    font-weight: 400;
    margin-bottom: 5vh;
    margin-top: 0;
}
.boxText img{
    width: 40px;
}
.boxText h4{
    font-size: 0.8em;
    color: rgb(196, 172, 131);
    margin-bottom: 10px;
}
.boxText .imgName{
    margin-top: 4vh;
    width: 350px;
}
.containerImg{
    width: 100vw;
    height: 100vh;
}
.containerImg video{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.uus{
    position: relative;
    inset: 0 auto;
    top: 50%;
    
    
    z-index: 100;
    text-align: center;
   
}
